import React, { useState, useEffect } from 'react';

import { 
    AppBar, 
    Button, 
    Tabs, 
    Tab,
    Link as MLink } from '@mui/material';

import { Box } from '@mui/system';

import DownloadIcon from '@mui/icons-material/Download';

import { Link } from 'react-router-dom';

const Header = () => {

    const [value, setValue] = useState(0);

    const handleChange = (e, newValue) => {
        setValue(newValue);
    }

    useEffect(() => {
        const paths = {
            '/': 0,
            '/proyectos': 1
        }

        let path = window.location.pathname;
        if (paths[path] !== value){
            setValue(paths[path]);
        }
        //eslint-disable-next-line
    }, []);

    return (
        <Box>
            <AppBar 
                color="primary" 
                position="fixed"
                sx={{
                    display: 'grid',
                    gridTemplateColumns: {
                        xs: '70% 30%',
                        md: '70% 30%',
                        lg: '80% 20%',
                        
                    }
                }}
            >
                <Tabs 
                    value={value}
                    onChange={handleChange} 
                    indicatorColor="secondary"
                    textColor="inherit"
                    centered
                    variant="fullWidth"
                >
                    <Tab 
                        sx={{
                            minHeight: '56px'
                        }}
                        component={Link} to="/" 
                        label="Home"
                    />
                    <Tab 
                        sx={{
                            minHeight: '56px'
                        }}
                        component={Link} to="/proyectos" 
                        label="Proyectos"
                    />
                </Tabs> 
                <Box
                    sx={{
                        mx: 2,
                        display: 'flex',
                        alignItems: 'center'
                    }}
                >
                    <Button 
                        color="inherit"
                        variant="outlined"
                        component={MLink}
                        href="/static/CVCamiloRojas.pdf"
                        download
                        endIcon={<DownloadIcon />}
                    >
                        CV
                    </Button>
                </Box>

            </AppBar>
             
            {/* <SwipeableDrawer 
                anchor="right"
                disableBackdropTransition={!iOS} 
                disableDiscovery={iOS}
                open={openDrawer}
                onClose={() => setOpenDrawer(false)}
                onOpen={() => setOpenDrawer(true)}
            >
                <List >
                    <ListItem 
                        onClick={() => {setOpenDrawer(false);}} 
                        divider 
                    >
                        <ListItemIcon>
                            <Avatar>hola</Avatar>
                        </ListItemIcon>
                        <ListItemText>
                           hola
                        </ListItemText>
                    </ListItem>
                </List>
            </SwipeableDrawer>  */}
            <Box sx={{minHeight: 90}}/> 
        </Box>
    );
}


export default Header;